import "./styles.css";
import {useState} from "react";

export default function App() {
  return (
    <div className="App">
      <Counter />
    </div>
  );
}

function Counter() {
  const [numCount, setCount] = useState(0);
  const [onOff, setOnOff] = useState("on");

function addCount() {
  let newCount = numCount + 1;
  setCount(newCount);  
} 

function newAddCount() {
  setCount(numCount + 10);  
} 


function change() {
  if (onOff === "on") {
    setOnOff("off");
  }else{
    setOnOff("on");
  }
}

  return (
    <div>
      <div>you have clicked the button {numCount} times</div>
      <button onClick={addCount}>click me</button>
      <button onClick={newAddCount}>add 10 more</button>
      <div>it's {onOff}</div>
      <button onClick={change}>click it</button>
    </div>
  );
}


